import directive from './directives';
import { permission } from './permission';

const importDirective = Vue => {
    /**
   *  v-draggable="options"
   * options = {
   *  trigger: /CSS/,
   *  body:    /CSS/,
   *  recover: //
   * }
   */
    Vue.directive('draggable', directive.draggable);
    Vue.directive('permission', permission);
};

export default importDirective;
