import Main from '@/components/main';

/**
 * iview-adminmeta:
 * meta: {
 *  title: { String|Number|Function }
 *         
 *         '{{  }}';
 *         
 *  hideInMenu: (false) true
 *  notCache: (false) true
 *  access: (null) 
 *  icon: (-) '_'
 *  beforeCloseName: (-) tab'@/router/before-close.js'
 * }
 */

export default [
    {
        path: '/login',
        name: 'login',
        meta: {
            title: 'Login - ',
            hideInMenu: true
        },
        component: () => import('@/nmedia/views/login.vue')
    },
    {
        path: '/forwardTemp',
        name: 'ForwardTemp',
        meta: {
            hideInMenu: true,
            title: '',
            access: ['m:temp:build:for', '*']
        },
        component: () => import('@/nmedia/views/temp/index.vue')
    },
    {
        path: '/',
        name: '_home',
        redirect: '/home',
        component: Main,
        meta: {
            hideInMenu: true,
            notCache: true
        },
        children: [
            {
                path: '/home',
                name: 'home',
                meta: {
                    title: '',
                    notCache: true,
                    icon: 'md-home'
                },
                component: () => import('@/nmedia/views/single-page/home.vue')
            }
        ]
    },
    {
        path: '/template',
        name: 'Template',
        meta: {
            title: '',
            access: ['m:temp', '*']
        },
        component: Main,
        children: [
            {
                path: 'store',
                name: 'TempStore',
                meta: {
                    icon: 'md-apps',
                    title: '',
                    access: ['m:temp:store', '*']
                },
                component: () => import('@/nmedia/views/temp/storeforward.vue')
            },
            {
                path: '',
                name: 'SelectTemplate',
                meta: {
                    hideInMenu: true,
                    title: '',
                    access: ['m:temp:build:select', '*'],
                    href: '#/forwardTemp'
                }
            }]
    },
    {
        path: '/content',
        name: 'Content',
        meta: {
            title: '',
            access: ['m:content', '*']
        },
        component: Main,
        children: [{
            path: 'publish',
            name: 'ContentPublish',
            meta: {
                icon: 'md-apps',
                title: '',
                access: ['m:content:pub', '*']
            },
            component: () => import('@/nmedia/views/content/pub.vue')
        },
        {
            path: 'list',
            name: 'PublishList',
            meta: {
                icon: 'md-apps',
                title: '',
                access: ['m:content:list', '*']
            },
            component: () => import('@/nmedia/views/content/list.vue')
        }]
    }, {
        path: '/product',
        name: 'Product',
        meta: {
            title: '',
            access: ['m:product', '*']
        },
        component: Main,
        children: [{
            path: 'addsubproduct',
            name: 'AddSubProduct',
            meta: {
                title: '',
                notTag: true,
                notCache: true,
                hideInMenu: true,
                access: ['m:product:sub:add', '*']
            },
            component: () => import('@/nmedia/views/product/addsubproduct.vue')
        }, {
            path: 'subproductlist',
            name: 'SubProductList',
            meta: {
                title: '',
                notTag: true,
                notCache: true,
                hideInMenu: true,
                access: ['m:product:sub:list', '*']
            },
            component: () => import('@/nmedia/views/product/subProductList.vue')
        }, {
            path: 'productlist',
            name: 'ProductList',
            meta: {
                icon: 'md-apps',
                title: '',
                access: ['m:product:list', '*']
            },
            component: () => import('@/nmedia/views/product/productList.vue')
        }]
    },
    {
        path: '/role',
        name: 'Role',
        meta: {
            title: '',
            access: ['m:nmedia:perm:role', '*']
        },
        component: Main,
        children: [
            {
                path: 'audit',
                name: 'RoleAudit',
                meta: {
                    icon: 'md-apps',
                    title: '',
                    access: ['m:nmedia:perm:role:audit:list', '*']
                },
                component: () => import('@/nmedia/views/role/aud.vue')
            },
            {
                path: 'allUserList',
                name: 'RoleAllUserList',
                meta: {
                    icon: 'md-apps',
                    title: '',
                    access: ['m:nmedia:perm:role:list', '*']
                },
                component: () => import('@/nmedia/views/role/alluser.vue')
            }]
    },
    {
        path: '/credit',
        name: 'Credit',
        meta: {
            title: '',
            access: ['m:nmedia:credit', '*']
        },
        component: Main,
        children: [
            {
                path: 'creditList',
                name: 'CreditList',
                meta: {
                    icon: 'md-apps',
                    title: '',
                    access: ['m:nmedia:credit:list', '*']
                },
                component: () => import('@/nmedia/views/credit/creditList.vue')
            }
        ]
    },
    {
        path: '/401',
        name: 'error_401',
        meta: {
            hideInMenu: true
        },
        component: () => import('@/error-page/401.vue')
    },
    {
        path: '/500',
        name: 'error_500',
        meta: {
            hideInMenu: true
        },
        component: () => import('@/error-page/500.vue')
    },
    {
        path: '*',
        name: 'error_404',
        meta: {
            hideInMenu: true
        },
        component: () => import('@/error-page/404.vue')
    }
];
