export const isWx = () => /MicroMessenger/i.test(navigator.userAgent);

// 
export const formatNumber = function (numbers) {
    if (numbers) { return Number(numbers / 100).toFixed(2); }
    return Number(0).toFixed(2);
};

//  4
export const formatNumberFour = function (numbers) {
    if (numbers) { return Number(numbers / 10000).toFixed(4); }
    return Number(0).toFixed(4);
};

/**
 * 
 * @param {Array} array 
 * @param {String | Number} pid ID :0
 * @param {String} parentProp  :pid
 * @param {String} idProp ID :id
 * @param {String} childProp  :children
 * @param {Function} renderer 
 */
export const MakeTree = (array, pid = 0, parentProp = 'pid', idProp = 'id', childProp = 'children', renderer) => {
    const result = [];
    let temp = [];
    for (const item of array) {
        if (item[parentProp] === pid || (pid === 0 && (item[parentProp] === undefined || item[parentProp] === null))) {
            const clone = Object.assign({}, item);
            result.push(clone);
            temp = MakeTree(array, item[idProp], parentProp, idProp, childProp);
            if (temp.length > 0) {
                clone[childProp] = temp;
            }
            if (typeof renderer === 'function') {
                renderer(clone);
            }
        }
    }
    return result;
};

export const PrefixInteger = (num, length) => (Array(length).join('0') + num).slice(-length);

/**
 * sha1
 * @param {*} file 
 * @param {*} progress 
 */
export const FileSha1 = (file, progress = (percent, loaded, total) => {}) => {
    const reader = new FileReader();
    return new Promise((resolve, reject) => {
        reader.onprogress = (e) => progress(Math.floor((e.loaded / e.total) * 100), e.loaded, e.total);
        reader.onloadend = (e) => {
            resolve(SHA1(WordArray.create(e.target.result)).toString());
        };
        reader.readAsArrayBuffer(file);
    });
};

/**
 * vue
 * @param {*} component 
 * @param {*} directive 
 */
export const findDirective = (component, directive) => {
    let directives = component.$vnode ? component.$vnode.data.directives || [] : [];
    if (Array.isArray(directives)) {
        directives = directives.filter(d => d.name === directive);
    }
    if (Array.isArray(component.$children)) {
        component.$children.map(c => findDirective(c, directive)).forEach(ds => directives.push(...ds));
    }
    return directives;
};

/**
 * keykey():
 * {name: 'Alone', roleNames: 'a,b'} = {name: 'Alone', role: [{ name: 'a' }, { name: 'b' }]}
 * @param {Object | Array} vo 
 * @param {String} prefix 
 */
export const collectionForVo = (vo, prefix, name) => {
    if (Array.isArray(vo)) {
        vo.forEach(v => collectionForVo(v, prefix));
    } else {
        let min = 0;
        const map = Object.keys(vo).filter(key => key.startsWith(prefix) && key.endsWith('s')).reduce((m, key) => {
            let newerKey = key.substring(prefix.length, key.length - 1);
            newerKey = `${newerKey.substr(0, 1).toLowerCase()}${newerKey.substring(1)}`;
            const val = vo[key].split(',');
            m.set(newerKey, val);
            min = Math.min(val.length);
            return m;
        }, new Map());
        vo[name || `${prefix}s`] = Array.from({ length: min }).map((n, index) => {
            const obj = Object.create(null);
            for (const [k, v] of map) {
                obj[k] = v[index];
            }
            return obj;
        });
    }
};

/**
 * 
 * @param {String} url 
 */
export const transformImgUrl = (url) => `${$config.IMG_ADDRESS}${url}`;

export const isDev = process.env.NODE_ENV === 'development';

export const params = qs.parse(window.location.search, {
    ignoreQueryPrefix: true
});

export const platform = isDev && params?.platform ? params.platform : $config.PLATFORM;

/**
 * 
 * @param plan 
 * @returns *
 */
export const calcEffective = plan => {
    let effectiveDate, expiryDate;
    const now = dayjs();
    const booking = dayjs(plan.start);
    const endTime = dayjs(plan.end);
    if (plan.effectiveType === 1) {
        effectiveDate = booking.isAfter(now) ? booking : now;
        expiryDate = booking.add(plan.effectiveDay - 1, 'day')
            .set('hour', endTime.hour())
            .set('minute', endTime.minute())
            .set('second', endTime.second());
    } else {
        // 
        const effectiveStart = dayjs(plan.effectiveStartDate);
        const effectiveEnd = dayjs(plan.effectiveEndDate);
        effectiveDate = booking.isAfter(effectiveStart) ? booking : effectiveStart;
        expiryDate = effectiveEnd;
    }
    // ,
    if (plan.useSecondLimit) {
        const newer = now.add(plan.useSecondLimit, 'second');
        effectiveDate = newer.isAfter(effectiveDate) ? newer : effectiveDate;
    }
    return {
        effectiveDate,
        effective: effectiveDate?.format('YYYYMMDD HHmmss') || '-',
        expiryDate,
        expiry: expiryDate?.format('YYYYMMDD HHmmss') || '-'
    };
};

/**
 * 
 * @param plans 
 * @returns {*}
 */
export const extractTimes = plans => plans.map(item => ({
    sold: item.sold,
    stock: item.stock,
    startTime: item.start,
    endTime: item.end,
    start: dayjs(item.start).format('HH:mm'),
    end: dayjs(item.end).format('HH:mm')
}));

/**
 * 
 * @param val
 * @returns {*[]}
 */
export const transformWeeks = val => {
    const str = Number(val).toString(2).padStart(7, '0');
    return Array.from(str).reduce((result, n, index) => [ ...result, ...(n === '1' ? [ Weeks[index] ] : []) ], []);
};

/**
 * 
 * @param data 
 * @param value 
 * @param field 
 * @param index 
 * @returns {number} 
 */
export const lastAdjacentIndex = (data, value, field, index) => {
    let last = 0;
    const slice = data.slice(index);
    for (let i = 0; i < slice.length; i++) {
        if (slice[i][field] === value) {
            last = index + i;
        } else {
            return last;
        }
    }
    return last;
};
